import React from 'react';
import './Reports.css';
import { Menu, Icon, Layout, Tree, Button, Card } from 'antd';
const { Sider, Content } = Layout;

export default class extends React.Component {
    state = {
        menu: 'r1'
    }

    render() {
        return (
            <Layout className="reports-layout">
                <Sider collapsible={true}>
                    {this.renderMenu()}
                </Sider>
                <Content>
                    {this.renderSelectedMenuItem()}
                </Content>
            </Layout>
        );
    }

    renderSelectedMenuItem() {
        switch (this.state.menu) {
            case "r1": return this.renderCookingReferenceReport()
            case "r1p2": return this.renderCookingReferenceReportPage2()
            case "r2": return this.renderTopFavoritesReport()
            default: return undefined
        }
    }

    renderMenu() {
        return (
            <Menu
                onClick={(e) => this.setState({ menu: e.key })}
                defaultSelectedKeys={['r1']}
                defaultOpenKeys={['r']}
                mode="inline" style={{ height: '100%' }}
            >
                <Menu.SubMenu
                    key="r"
                    title={
                        <span>
                            <Icon type="heart" theme="twoTone" twoToneColor="#eb2f96" />
                            <span>Recipe Reports</span>
                        </span>
                    }
                >
                    <Menu.Item key="r1">Cooking Reference</Menu.Item>
                    <Menu.Item key="r2">Top Favorites</Menu.Item>
                </Menu.SubMenu>
            </Menu>
        );
    }

    renderCookingReferenceReport() {
        const check = (e) => {
            if (e && e.length === 1 && e[0] === '0-0') {
                this.setState({ r1checkedKeys: window.foodnet.recipes.map(it => it.id.toString()) });
            }
            else {
                this.setState({ r1checkedKeys: e })
            }
        };
        return (
            <div className="recipe-select">
                <Tree
                    checkable
                    onSelect={(e) => check(e)}
                    onCheck={(e) => check(e)}
                    expandedKeys={['0-0']}
                    checkedKeys={this.state.r1checkedKeys}
                >
                    <Tree.TreeNode title="All Recipes" key="0-0">
                        {window.foodnet.recipes.map(r => (
                            <Tree.TreeNode title={r.name} key={r.id} />
                        ))}
                    </Tree.TreeNode>
                </Tree>
                <Button disabled={!this.state.r1checkedKeys || this.state.r1checkedKeys.length < 1} onClick={() => { this.setState({ menu: 'r1p2' }) }}>Generate Report</Button>
            </div>
        )
    }

    renderCookingReferenceReportPage2() {
        return (
            <div className="recipe-report">
                {window.foodnet.recipes.filter(it => this.state.r1checkedKeys.indexOf(it.id.toString()) !== -1).map(r => (
                    <Card key={r.id} size="small" >
                        <div className="recipe" key={r.id}>
                            <div className="recipe-name">Recipe Name:</div>
                            <div>{r.name}</div>
                            <div className="desc-header">Instructions:</div>
                            <div className="desc-body">{r.desc || "None"}</div>
                            <div className="ing">Ingredients:</div>
                            <div className="ingredient-list">
                                {r.ingredients.map(i => (
                                    <div key={i.id}>
                                        <div>
                                            {window.foodnet.ingredients.find(it => it.id === i.id).name}, {i.qty} {window.foodnet.measurements.find(it => it.id === i.type).name}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </Card>
                ))}
            </div>
        );
    }

    renderTopFavoritesReport() {
        return <div>Coming Soon</div>
    }
}