import React from 'react';
import './AppHeader.css';
import { Menu, Icon } from 'antd';
import { withRouter } from 'react-router-dom';

export default withRouter(props => <AppHeader {...props} />);

class AppHeader extends React.Component {
    componentDidMount() {
        // console.log(this.props.location);
    }
    render() {
        const handleClick = (key) => {
            switch (key) {
                case "recipes":
                    this.props.history.push("");
                    break;
                case "planner":
                    this.props.history.push("Planner");
                    break;
                case "shopper":
                    this.props.history.push("Shopper");
                    break;
                case "inventory":
                    this.props.history.push("Inventory");
                    break;
                case "importexport":
                    this.props.history.push("ImportExport");
                    break;
                case "reports":
                    this.props.history.push("Reports");
                    break;
                case "about":
                    this.props.history.push("About");
                    break;
                default:
            }
        }
        const findSelectionFromRoute = () => {
            const path = this.props.location.pathname.substring(1)
            switch (path) {
                case "ImportExport":
                case 'Reports':
                case 'Planner':
                case 'Shopper':
                case 'About':
                case 'Inventory':
                    return path.toLowerCase()
                default:
                    return "recipes"
            }
        }
        return (
            <Menu onClick={(e) => handleClick(e.key)} selectedKeys={[findSelectionFromRoute()]} mode="horizontal">
                <Menu.Item key="recipes">
                    <Icon type="heart" theme="twoTone" twoToneColor="#eb2f96" />
                    Recipes
                </Menu.Item>
                <Menu.Item key="planner">
                    <Icon type="calendar" theme="twoTone" />
                    Planner
                </Menu.Item>
                <Menu.Item key="shopper">
                    <Icon type="shopping" theme="twoTone" />
                    Shopper
                </Menu.Item>
                <Menu.Item key="inventory">
                    <Icon type="pic-right" />
                    Inventory
                </Menu.Item>
                <Menu.Item key="reports">
                    <Icon type="align-left" />
                    Reports
                </Menu.Item>
                <Menu.Item key="importexport">
                    <Icon type="setting" />
                    Import/Export
                </Menu.Item>
                <Menu.Item key="about">
                    <Icon type="question" />
                    About
                </Menu.Item>
            </Menu>
        )
    }
}