import React from 'react';
import { Tag, Input, Tooltip, Icon } from 'antd';

export default class extends React.Component {
    state = {
        tagColors: ["blue", "green", "red", "orange", "cyan", "purple"],
    }

    render() {
        const handleInputConfirm = () => {
            const { inputValue } = this.state;
            this.setState({
                inputVisible: false,
                inputValue: '',
            });
            if (inputValue && inputValue.trim().length > 0)
                this.props.add(inputValue.trim());
        }
        return (
            <div className={this.props.className || ""}>
                {this.props.tags.map((tag, index) => {
                    const isLongTag = tag.length > 20;
                    const tagElem = (
                        <Tag key={tag} color={this.state.tagColors[index % this.state.tagColors.length]} closable={true} onClose={() => this.props.remove(tag)}>
                            {isLongTag ? `${tag.slice(0, 20)}...` : tag}
                        </Tag>
                    );
                    return isLongTag ? (
                        <Tooltip title={tag} key={tag}>
                            {tagElem}
                        </Tooltip>
                    ) : (tagElem);
                })}
                {this.state.inputVisible && (
                    <Input
                        ref={input => (this.input = input)}
                        type="text"
                        size="small"
                        style={{ width: 78 }}
                        value={this.state.inputValue}
                        onChange={(e) => this.setState({ inputValue: e.target.value })}
                        onBlur={handleInputConfirm}
                        onPressEnter={handleInputConfirm}
                    />
                )}
                {!this.state.inputVisible && (
                    <Tag onClick={() => this.setState({ inputVisible: true }, () => this.input.focus())} style={{ background: '#fff', borderStyle: 'dashed' }}>
                        <Icon type="plus" /> New Tag
                    </Tag>
                )}
            </div>
        );
    }
}