import React from 'react';
import './About.css'
import { Card } from 'antd'

export default class extends React.Component {
    render() {
        return (
            <div className="about-page">
                <Card>
                    {this.createBlurb()}
                    {this.contact()}
                    {this.donate()}
                </Card>
            </div>
        );
    }
    createBlurb() {
        return (
            <blockquote className="about-desc">
                The goal of this app is to make meal creation for yourself and your family easier, and to minimize the time spent doing so.
                This is a completely 100% client-side app capable of going offline. No ads, spyware, or other bullonky.
            </blockquote>
        )
    }
    contact() {
        // eslint-disable-next-line no-useless-concat
        const email = "harry.h.bowyer" + "@g" + "mail.com";
        const subject = "subject=Foodnet Feedback";
        const body = "body=Concerning your food app,%0A%0A%0A%0A";
        return (
            <div className="email"><a href={`mailto://${email}?${subject}&${body}`}>Contact Link (e-mail)</a></div>
        )
    }
    donate() {
        return (
            <>
                {/* <a target="_blank" href="https://www.paypal.com/cgi-bin/webscr?cmd=_donations&business=K6K82W4JR45SN&currency_code=USD&source=url">
                    <img src="https://www.paypalobjects.com/en_US/i/btn/btn_donateCC_LG.gif" />
                </a> */}
                <blockquote className="donate-desc">
                    Please consider buying me a coffee. It will help deprive me of sleep so that I can continue improving the app.
                    <br />
                    <span className="donate-desc-2">For now, email me using the link above if you wish to motivate me with material objects. Just knowing you are willing to do so helps. I'm in the process of figuring out how to filter funds through <b>The Man</b>.</span>
                </blockquote>
            </>
        )
    }
}