import React from 'react';
import { Form, Button, Input, Select, message } from 'antd';
import {isIngredientUsed} from '../admin/EntityUsageChecker'

export default class extends React.Component {
    state = {

    }

    componentDidMount() {

    }

    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 12 },
            },
        };
        const isNameUnique = () => {
            let takenNames = window.foodnet.ingredients.map(it => it.name);
            if (this.props.edit) {
                takenNames = window.foodnet.ingredients.filter(it => it.id !== this.state.id).map(it => it.name);
            }
            return takenNames.indexOf(this.state.name) === -1;
        }
        const submit = () => {
            if (this.props.edit) {
                const i = window.foodnet.ingredients.find(it => it.id === this.state.id);
                i.name = this.state.name.trim();
                this.props.close();
            }
            else {
                window.foodnet.ingredients.push({
                    id: window.foodnet.ingredients.length === 0 ? 1 : Math.max(...window.foodnet.ingredients.map(it => it.id)) + 1,
                    name: this.state.name.trim(),
                });
                window.foodnet.sync();
                this.props.close();
            }
        }
        return (
            <>
                <Form {...formItemLayout}>
                    {this.props.edit && this.renderPicker()}
                    <Form.Item
                        label={this.props.edit ? "New Name" : "Name"}
                        validateStatus={this.state.name && isNameUnique() ? "success" : "error"}
                        hasFeedback
                        help={this.state.name && isNameUnique() ? undefined : !this.state.name ? "Should not be blank" : "Name should be unique"}
                    >
                        <Input autoFocus value={this.state.name} placeholder="Enter a name"
                            onChange={(v) => this.setState({ name: v.target.value })}
                            onPressEnter={() => {
                                if (!this.validate()) {
                                    submit();
                                }
                            }}
                        />
                    </Form.Item>
                </Form>
                <div className="footer">
                    {this.props.edit && <Button key="delete" type="danger" disabled={!this.state.id} onClick={() => {
                        if (isIngredientUsed(this.state.id)) {
                            message.error("This ingredient is still being used. Please remove, replace, or rename the ingredient where it is being used before deleting.")
                        }
                        else {
                            window.foodnet.ingredients = window.foodnet.ingredients.filter(it => it.id !== this.state.id)
                            window.foodnet.sync()
                            this.props.close()
                        }
                    }}>Delete</Button>}
                    <Button key="back" onClick={() => this.props.close()}>Cancel</Button>
                    <Button type="primary" disabled={this.validate()} onClick={() => submit()}>
                        Submit
                    </Button>
                </div>
            </>
        );
    }

    renderPicker() {
        return (
            <Form.Item label="Choose existing ingredient" validateStatus={(this.props.edit && !this.state.id) ? 'error' : 'success'} hasFeedback={true}>
                <Select value={this.state.id ? this.state.id.toString() : undefined} onChange={v => {
                    this.setState({ id: Number(v), name: window.foodnet.ingredients.find(it => it.id === Number(v)).name });
                }}>
                    {window.foodnet.ingredients.map(i => (
                        <Select.Option key={i.id} >{i.name}</Select.Option>
                    ))}
                </Select>
            </Form.Item>
        );
    }

    validate() {
        return !this.state.name || (this.props.edit && !this.state.id);
    }
}