/* eslint-disable eqeqeq */
import React from 'react';
import './Shopper.css'
import { DatePicker, Checkbox, Dropdown, Modal, Button, Menu, Tag, Switch, Icon } from 'antd'
import moment from 'moment'
import Tags from './Tags'
import ReactDragListView from '../hoc/ReactDragListView'
import MyInput from './CustomInput'

const { RangePicker } = DatePicker

export default class extends React.Component {
    state = {
        checkedItems: [],
        filteredTags: [],
        MULTIPLIER: 4,
    }
    componentDidMount() {
        this.setState({
            dates: window.foodnet.plannerDates && window.foodnet.plannerDates.length === 2 ? window.foodnet.plannerDates.map(it => moment(it)) : undefined,
            checkedItems: window.foodnet.plannerCheckedItems || [],
            customSort: window.foodnet.customSort,
            customSortOrder: window.foodnet.customSortOrder,
        })
    }
    render() {
        const updateTags = (tag, checked) => {
            let tags = this.state.filteredTags;
            if (checked) {
                tags = tags.concat(tag)
            }
            else {
                tags = tags.filter(t => t !== tag)
            }
            this.setState({ filteredTags: tags })
        }

        const tags = this.getTags()

        const createMenu = () => {
            return [(
                <Menu.Item key={"_NO_TAG"}><Checkbox onChange={e => updateTags("_NO_TAG", e.target.checked)}>Untagged</Checkbox></Menu.Item>
            )].concat(tags.map(tag => (
                <Menu.Item key={tag}><Checkbox onChange={e => updateTags(tag, e.target.checked)}>{tag}</Checkbox></Menu.Item>
            )))
        }

        const onDragEnd = (fromIndex, toIndex) => {
            const newTags = [...tags]
            const item = newTags.splice(fromIndex / this.state.MULTIPLIER, 1)[0];
            newTags.splice(toIndex / this.state.MULTIPLIER, 0, item);
            this.setState({ customSortOrder: newTags });
        }

        const onInputChange = (newValue, oldValue) => {
            if (Number(newValue) !== oldValue) {
                const fromIndex = oldValue - 1;
                let toIndex = Number(newValue) - 1;
                if (toIndex >= tags.length) {
                    toIndex = tags.length - 1;
                }
                onDragEnd(fromIndex * this.state.MULTIPLIER, toIndex * this.state.MULTIPLIER);
            }
        }

        return (
            <div className="shopper">
                <div className="buttons">
                    <Button disabled={!tags || tags.length === 0} onClick={() => this.setState({ modal: 'sort' })}>Sort by Tags</Button>
                    <Dropdown
                        disabled={createMenu().length === 1}
                        onVisibleChange={(v) => this.setState({ dropdownvisible: v })}
                        visible={this.state.dropdownvisible}
                        trigger={['click']}
                        overlay={(
                            <Menu>
                                {createMenu()}
                            </Menu>
                        )}>
                        <Button>Filter by Tags</Button>
                    </Dropdown>
                    <Button onClick={() => this.setState({ hideTags: !this.state.hideTags })}>{this.state.hideTags ? 'Show Tags' : 'Hide Tags'}</Button>
                </div>
                <RangePicker value={this.state.dates} onChange={(m) => {
                    this.setState({ dates: m, checkedItems: [] })
                    window.foodnet.plannerDates = m.map(it => it.format());
                    window.foodnet.plannerCheckedItems = [];
                    window.foodnet.sync();
                }} />
                {this.state.dates && (
                    this.createIngredientList()
                )}

                {this.state.modal === 'sort' && (
                    <Modal className="sort-popup" visible={true} onCancel={() => {
                        this.setState({ modal: undefined, customSort: window.foodnet.customSort, customSortOrder: window.foodnet.customSortOrder })
                    }} onOk={() => {
                        window.foodnet.customSort = this.state.customSort
                        window.foodnet.customSortOrder = this.state.customSortOrder
                        window.foodnet.sync()
                        this.setState({ modal: undefined })
                    }}>
                        <div><Switch checked={this.state.customSort} onChange={(checked) => {
                            this.setState({ customSort: checked, customSortOrder: tags })
                        }} /> Enable Tag Sorting</div>
                        <ReactDragListView
                            onDragEnd={onDragEnd}
                            nodeSelector="span"
                            handleSelector="span"
                        >
                            <div className="drag-list">
                                {tags.map((item, index) => (
                                    <React.Fragment key={index}>
                                        <MyInput type="number" min="1" max="9999" defaultValue={index + 1} onPressEnter={(e) => onInputChange(e.target.value, index + 1)} onBlur={(e) => onInputChange(e.target.value, index + 1)} />
                                        <Tag>{item}</Tag>
                                        <Icon className="up" type="up" onClick={() => onDragEnd(index * this.state.MULTIPLIER, index * this.state.MULTIPLIER - this.state.MULTIPLIER)} />
                                        <Icon className="down" type="down" onClick={() => onDragEnd(index * this.state.MULTIPLIER, index * this.state.MULTIPLIER + this.state.MULTIPLIER)} />
                                    </React.Fragment>
                                ))}
                            </div>
                        </ReactDragListView>
                    </Modal>
                )}
            </div>
        );
    }

    getTags() {
        let tags = []
        window.foodnet.ingredients.forEach(ing => {
            (ing.tags || []).forEach(tag => {
                if (tags.indexOf(tag) === -1) tags = tags.concat(tag);;
            })
        });
        (this.state.filteredTags || []).forEach(t => {
            if (tags.indexOf(t) === -1) tags = tags.concat(t);;
        });
        tags = tags.sort((a, b) => a.localeCompare(b))
        if (this.state.customSort && this.state.customSortOrder && this.state.customSortOrder.length > 0) {
            const { customSortOrder } = this.state;
            tags = tags.sort((a, b) => {
                const ai = customSortOrder.indexOf(a)
                const bi = customSortOrder.indexOf(b)
                if (ai !== -1 || bi !== -1) {
                    if (ai !== -1 && bi !== -1) {
                        return ai < bi ? -1 : 1;
                    }
                    return ai === -1 ? -1 : 1;
                }
                return 0;
            })
        }
        return tags;
    }

    createIngredientList() {
        const { foodnet } = window;
        let list = [];
        foodnet.events.forEach(event => {
            if (moment(event.date).isBetween(...this.state.dates, 'days', '[]')) {
                const multiplier = Number(event.qty) || 0;
                const recipe = foodnet.recipes.find(r => r.id === event.recipe);
                recipe.ingredients.forEach(ing => {
                    list.push({ id: ing.id, type: ing.type, qty: Number(ing.qty) * multiplier });
                })
            }
        })

        const mergeDuplicates = (item, index, l) => {
            if (index > 0 && l[index - 1].id === item.id && l[index - 1].type === item.type) {
                let newIndex = index - 1;
                while (l[newIndex].deleted) newIndex--;
                l[newIndex].qty += item.qty;
                item.deleted = true;
            }
            return item;
        };
        list = list.sort((a, b) => {
            if (a.id !== b.id) {
                return a.id < b.id ? -1 : 1;
            }
            if (a.type !== b.type) {
                return a.type < b.type ? -1 : 1;
            }
            return 0;
        }).map(mergeDuplicates).filter(item => !item.deleted);

        const sortedTags = this.getTags()

        return (
            <ul className="ingredient-list">
                {list
                    .map(item => ({
                        ...item,
                        name: foodnet.ingredients.find(i => i.id === item.id).name,
                        typename: foodnet.measurements.find(m => m.id === item.type).name,
                        checked: this.state.checkedItems.indexOf(item.id) !== -1,
                        tags: foodnet.ingredients.find(i => i.id === item.id).tags || []
                    }))
                    .sort((a, b) => a.name.localeCompare(b.name) || a.typename.localeCompare(b.typename))
                    .filter(item => {
                        if (this.state.filteredTags.length === 0) return true;
                        let tags = item.tags
                        if (tags.length === 0) tags = ['_NO_TAG']
                        let found = false;
                        tags.forEach(t => {
                            this.state.filteredTags.forEach(t2 => {
                                if (t === t2) found = true;
                            })
                        })
                        return found;
                    })
                    .sort((a, b) => {
                        if (!this.state.customSort) {
                            return 0;
                        }
                        if (!a.tags || !b.tags || a.tags.length === 0 || b.tags.length === 0) {
                            if ((!a.tags || a.tags.length === 0) && (!b.tags || b.tags.length === 0)) {
                                return 0;
                            }
                            return !a.tags || a.tags.length === 0 ? 1 : -1;
                        }
                        const findHighestTag = (tags) => tags.reduce((tag, highestTag) => {
                            if (!highestTag) return tag;
                            const ti = sortedTags.indexOf(tag)
                            const hi = sortedTags.indexOf(highestTag)
                            if (ti === -1 || hi === -1) {
                                if (ti === -1 && hi === -1) return highestTag;
                                return ti === -1 ? highestTag : tag;
                            }
                            return ti < hi ? tag : highestTag;
                        })
                        const aTag = findHighestTag(a.tags)
                        const bTag = findHighestTag(b.tags)
                        const ai = sortedTags.indexOf(aTag)
                        const bi = sortedTags.indexOf(bTag)
                        if (ai !== -1 || bi !== -1) {
                            if (ai !== -1 && bi !== -1) {
                                return ai < bi ? -1 : 1;
                            }
                            return ai === -1 ? -1 : 1;
                        }
                        return 0;
                    })
                    .map((item, index) => (
                        <li key={index} className="shopper-list-items">
                            <Checkbox className="shopper-list-item" key={index} checked={item.checked} onChange={e => {
                                foodnet.plannerCheckedItems = foodnet.plannerCheckedItems.filter(it => it !== item.id)
                                if (e.target.checked) {
                                    foodnet.plannerCheckedItems = foodnet.plannerCheckedItems.concat(item.id);
                                }
                                this.setState({ checkedItems: foodnet.plannerCheckedItems });
                                foodnet.sync();
                            }}>
                                {`${item.name}: ${item.qty} ${item.typename}`}
                            </Checkbox>
                            {!this.state.hideTags && <Tags className="shopper-tags" tags={item.tags} add={(tag) => {
                                const ing = foodnet.ingredients.find(i => i.id === item.id)
                                foodnet.ingredients = foodnet.ingredients.filter(it => it.id !== item.id).concat({ ...ing, tags: [...item.tags].concat(tag) })
                                foodnet.sync()
                                this.forceUpdate()
                            }} remove={(tag) => {
                                const ing = foodnet.ingredients.find(i => i.id === item.id)
                                foodnet.ingredients = foodnet.ingredients.filter(it => it.id !== item.id).concat({ ...ing, tags: [...item.tags].filter(it => it !== tag) })
                                foodnet.sync()
                                this.forceUpdate()
                            }} />}
                        </li>
                    ))}
            </ul>
        );
    }
}