import React from 'react';
import { Form, Button, Input, Dropdown, Menu, Select, Modal, Icon, Table } from 'antd';
import './RecipeForm.css';
import IngredientForm from './IngredientForm'
import TypeForm from './TypeForm'

export default class extends React.Component {
    state = {
        ingredients: []
    }

    componentDidMount() {
        if (this.props.edit && !this.state.name) {
            const recipe = window.foodnet.recipes.find(r => r.id === this.props.edit);
            this.setState({ ...recipe });
        }
    }

    render() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 12 },
            },
        };
        return (
            <>
                <Form {...formItemLayout} className="recipe-form">
                    <Form.Item
                        label="Name"
                        validateStatus={this.state.name ? "success" : "error"}
                        hasFeedback
                        help={this.state.name ? undefined : "Should not be blank"}
                    >
                        <Input placeholder="Enter a name" value={this.state.name} onChange={(v) => this.setState({ name: v.target.value })} />
                    </Form.Item>

                    {this.renderConfigMenu()}

                    {this.renderMultichooser()}
                    {this.renderTable()}
                    <Form.Item className="instructions" label="Instructions">
                        <Input.TextArea
                            value={this.state.desc}
                            autoSize={{ minRows: 2, maxRows: 6 }}
                            onChange={v => this.setState({ desc: v.target.value })}
                        />
                    </Form.Item>
                </Form>
                <div className="footer">
                    <Button key="back" onClick={() => this.props.close()}>Cancel</Button>
                    <Button type="primary" disabled={this.validateForm()} onClick={() => {
                        if (this.state.id) {
                            window.foodnet.recipes = window.foodnet.recipes.filter(it => it.id !== this.state.id).concat({ ...this.state, name: this.state.name.trim() });
                        }
                        else {
                            const newId = window.foodnet.recipes.length === 0 ? 1 : Math.max(...window.foodnet.recipes.map(r => r.id)) + 1;
                            window.foodnet.recipes.push({ ...this.state, name: this.state.name.trim(), id: newId });
                        }
                        window.foodnet.sync();
                        this.props.close();
                    }}>
                        Submit
                    </Button>
                </div>
                {this.renderModal()}
                {this.renderTModal()}
            </>
        );
    }

    renderConfigMenu() {
        return (
            <div className="config">
                Configure Ingredients/Measurements
                <Dropdown trigger={['click']} overlay={
                    <Menu onClick={(e) => {
                        switch (e.key) {
                            case "1":
                                this.setState({ modal: 'add' })
                                break;
                            case "2":
                                this.setState({ modal: 'edit' })
                                break;
                            case "3":
                                this.setState({ tmodal: 'add' })
                                break;
                            case "4":
                                this.setState({ tmodal: 'edit' })
                                break;
                            default:
                        }
                    }}>
                        <Menu.Item key="1">
                            <Icon type="plus" />Create New Ingredient
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Icon type="edit" /> Edit Existing Ingredients
                        </Menu.Item>
                        <Menu.Divider />
                        <Menu.Item key="3">
                            <Icon type="plus" /> Create New Measurement
                        </Menu.Item>
                        <Menu.Item key="4">
                            <Icon type="edit" /> Edit Existing Measurements
                        </Menu.Item>
                    </Menu>
                }>
                    <Icon type="setting" className="setting-icon" />
                </Dropdown>
            </div>
        );
    }

    renderModal() {
        return this.state.modal && (
            <Modal width="90%" visible={true} footer={null} onCancel={() => this.setState({ modal: undefined })}>
                <IngredientForm edit={this.state.modal === 'edit' ? true : undefined} close={() => this.setState({ modal: undefined })} />
            </Modal>
        )
    }

    renderTModal() {
        return this.state.tmodal && (
            <Modal width="90%" visible={true} footer={null} onCancel={() => this.setState({ tmodal: undefined })}>
                <TypeForm edit={this.state.tmodal === 'edit' ? true : undefined} close={() => this.setState({ tmodal: undefined })} />
            </Modal>
        )
    }

    renderTable() {
        const wrapFormItemOnError = (cmp, validation, errorText) => {
            return (
                <Form.Item validateStatus={!validation ? "error" : "success"} help={!validation ? errorText : ""}>
                    {cmp}
                </Form.Item>
            )
        };
        const columns = [
            {
                title: 'Name',
                dataIndex: 'id',
                key: 'name',
                render: id => {
                    const i = window.foodnet.ingredients.find(it => it.id === id);
                    return <div>{i.name}</div>
                }
            },
            {
                title: 'Amount',
                dataIndex: 'id',
                key: 'qty',
                render: id => {
                    const i = this.state.ingredients.find(it => it.id === id);
                    return (
                        wrapFormItemOnError(<Input type="number" value={i.qty} onChange={v => {
                            this.setState({
                                ingredients: this.state.ingredients.filter(it => it.id !== id).concat([{
                                    ...this.state.ingredients.find(it => it.id === id),
                                    qty: v.target.value
                                }])
                            })
                        }} />, i.qty, "Should not be blank")
                    );
                }
            },
            {
                title: 'Type',
                dataIndex: 'id',
                key: 'type',
                render: id => {
                    const i = this.state.ingredients.find(it => it.id === id);
                    return (
                        wrapFormItemOnError(<Select
                            placeholder="Type"
                            filterOption={(inputValue, option) => {
                                const name = option.props.children;
                                const inp = typeof inputValue === 'string' || inputValue instanceof String ? inputValue : inputValue.toString();
                                return (name || "").toLowerCase().indexOf((inp || "").toLowerCase());
                            }}
                            value={i.type ? i.type.toString() : undefined}
                            onChange={(v) => {
                                this.setState({
                                    ingredients: this.state.ingredients
                                        .filter(z => z.id !== i.id)
                                        .concat(this.state.ingredients
                                            .filter(z => z.id === i.id)
                                            .map(z => ({ ...z, type: Number(v) })))
                                })
                            }}
                        >
                            {window.foodnet.measurements
                                .sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1)
                                .map(m => <Select.Option key={m.id}>{m.name}</Select.Option>)}
                        </Select>, i.type, "Should not be blank")
                    );
                }
            },
        ];
        const data = this.state.ingredients.sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1);
        return this.state.ingredients.length > 0 && (
            <Table columns={columns} dataSource={data} pagination={false} rowKey="id" />
        );
    }

    renderMultichooser() {
        return (
            <Select
                className="multi-chooser"
                mode="multiple"
                placeholder={window.foodnet.ingredients.length === 0 ? "Use the gear menu to create new ingredients" : "Please select"}
                value={this.state.ingredients.sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1).map(it => ({ key: it.id }))}
                disabled={window.foodnet.ingredients.length === 0}
                onChange={(values) => {
                    const ids = values.map(it => Number(it.key));
                    const idsToKeep = this.state.ingredients.map(it => it.id).filter(it => ids.indexOf(it) !== -1);
                    const idsToAdd = ids.filter(id => this.state.ingredients.map(it => it.id).indexOf(id) === -1);
                    this.setState({
                        ingredients: this.state.ingredients
                            .filter(it => idsToKeep.indexOf(it.id) !== -1)
                            .concat(
                                idsToAdd.map(it2 => (
                                    { ...window.foodnet.ingredients.find(it3 => it3.id === it2) }
                                ))
                            )
                            .sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1)
                    })
                }}
                labelInValue={true}
            >
                {window.foodnet.ingredients.sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1).map(it => (
                    <Select.Option key={it.id} value={it.id}>{it.name}</Select.Option>
                ))}
            </Select>
        );
    }

    renderIngAddButton() {
        return (
            <Button onClick={() => {
                this.setState({ modal: 'add' })
            }}>Create New Ingredient</Button>
        );
    }

    renderIngEditButton() {
        return (
            <Button onClick={() => {
                this.setState({ modal: 'edit' })
            }}>Edit Existing Ingredients</Button>
        );
    }

    renderTypeAddButton() {
        return (
            <Button onClick={() => {
                this.setState({ tmodal: 'add' })
            }}>Create New Measurement</Button>
        );
    }

    renderTypeEditButton() {
        return (
            <Button onClick={() => {
                this.setState({ tmodal: 'edit' })
            }}>Edit Existing Measurements</Button>
        );
    }

    validateForm() {
        const isValid = this.state.name && this.state.name.trim().length > 0 && this.state.ingredients && this.state.ingredients.length > 0;
        if (!isValid) return true;
        for (var i in (this.state.ingredients || [])) {
            const item = this.state.ingredients[i];
            if (!item.type || !item.qty) {
                return true;
            }
        }
        return false;
    }
}