import React from 'react';
import { save, load } from '../loaders/ExcelLoader';
import { Modal, Button, Form, Input, AutoComplete } from 'antd';
import './ImportExport.css';
import axios from 'axios'

export default class extends React.Component {
    state = {
        filename: 'backup.xlsx',
        allowedFileExtensions: [
            'xlsx', 'xlsm', 'xlsb', 'xml', 'xls', 'xlw', 'csv', 'txt', 'dif', 'sylk', 'slk', 'prn', 'ods', 'fods', 'dbf', 'eth'
        ].map(it => '.' + it),
    }
    render() {
        if (!window.File || !window.FileReader || !window.FileList || !window.Blob) {
            return (
                <div>
                    Error: your browser does not support the File API specification. It is required for long term storage.
                </div>
            )
        }
        return (
            <div className="import-export">
                <Button onClick={() => this.setState({ saveModal: true })}>
                    Export To Excel
                </Button>
                <Button onClick={() => this.setState({ loadModal: true })}>
                    Import From Excel
                </Button>
                <Button onClick={() => this.setState({ urlModal: true })}>
                    Import From Url (Excel)
                </Button>
                {this.state.saveModal && (
                    <Modal className="save-modal" visible={true} onCancel={() => this.setState({ saveModal: false })} onOk={() => { save(this.getFilename()); this.setState({ saveModal: false }) }}>
                        <Form.Item label="File Name">
                            <Input value={this.state.filename} onChange={v => this.setState({ filename: v.target.value })} />
                        </Form.Item>
                    </Modal>
                )}
                {this.state.loadModal && (
                    <Modal className="load-modal" visible={true} onCancel={() => this.setState({ loadModal: false })} okButtonProps={{ disabled: !this.state.file }} onOk={() => {
                        var fileReader = new FileReader();
                        fileReader.onload = function (e) {
                            load(e.target.result);
                        };
                        fileReader.readAsArrayBuffer(this.state.file);
                        this.setState({ loadModal: false })
                    }}>
                        <Form.Item label="">
                            <Input type="file" id="file" name="file" accept={this.state.allowedFileExtensions.join(',')} onChange={e => {
                                this.setState({ file: e.target.files[0] })
                            }} />
                        </Form.Item>
                    </Modal>
                )}
                {this.state.urlModal && this.urlModal()}
            </div>
        );
    }

    urlModal() {
        const dropboxHack = (url) => {
            if (url && url.indexOf("www.dropbox.com")) {
                return url.replace("www.dropbox.com", "dl.dropboxusercontent.com");
            }
        }
        const validateUrl = () => {
            if (!this.state.url || this.state.url.length === 0) {
                return false;
            }
            if (this.state.urlError) {
                return false;
            }
            if (!this.state.url.startsWith("http")) {
                return false;
            }
            if (this.state.url.indexOf("://") === -1) {
                return false;
            }
            return true;
        }
        const validateMsg = () => {
            if (!this.state.url || this.state.url.length === 0) {
                return "Cannot be blank";
            }
            if (this.state.urlError) {
                return undefined;
            }
            if (!this.state.url.startsWith("http")) {
                return "URL must begin with http or https";
            }
            if (this.state.url.indexOf("://") === -1) {
                return "URL is not in the correct format (i.e. http://example.com)";
            }
        }
        const submit = () => {
            axios.get(dropboxHack(this.state.url), { responseType: 'arraybuffer' }).then((r) => {
                if (r.status !== 200) {
                    this.setState({ urlError: `${r.status ? r.response.status + " - " : ""}${r.statusText ? r.response.statusText + ": " : ""}${r.data || ""}` })
                }
                else {
                    try {
                        load(r.data)
                        if (window.foodnet.importUrls.indexOf(this.state.url) === -1) {
                            window.foodnet.importUrls.push(this.state.url);
                            window.foodnet.sync();
                        }
                        this.setState({ urlModal: undefined, url: undefined })
                    }
                    catch (error) {
                        console.log(error);
                        this.setState({ urlError: "Could not parse file: " + error })
                    }
                }
            }).catch((e) => {
                console.log(e)
                if (!e.response) {
                    e.response = {
                        statusText: "error: CORS blocking"
                    }
                }
                console.log(`${e.response.status ? e.response.status + " - " : ""}${e.response.statusText ? e.response.statusText + ": " : ""}${e.response.data || ""}`)
                this.setState({ urlError: `${e.response.status ? e.response.status + " - " : ""}${e.response.statusText ? e.response.statusText : ""}` })
            })
        }
        return (
            <Modal className="url-modal" visible={true} onCancel={() => this.setState({ urlModal: false })} okButtonProps={{ disabled: !validateUrl() }} onOk={() => submit()}>
                <Form.Item label="Remote URL" validateStatus={validateUrl() ? 'success' : 'error'} help={this.state.urlError || validateMsg()}>
                    <AutoComplete style={{ width: 200 }} value={this.state.url} onChange={e => {
                        this.setState({ url: e, urlError: undefined })
                    }} >
                        {window.foodnet.importUrls.map(it => (
                            <AutoComplete.Option key={it}>{it}</AutoComplete.Option>
                        ))}
                    </AutoComplete>
                    <Button className="url-delete-button" onClick={() => {
                        window.foodnet.importUrls = window.foodnet.importUrls.filter(it => it !== this.state.url)
                        window.foodnet.sync()
                        this.setState({url: undefined})
                    }} icon="delete" disabled={!this.state.url}/>
                </Form.Item>
            </Modal>
        )
    }

    getFilename() {
        const { filename } = this.state;
        for (let e in this.state.allowedFileExtensions) {
            if (filename.endsWith(this.state.allowedFileExtensions[e])) {
                return filename;
            }
        }
        return filename + ".xlsx";
    }
}