import React from 'react';
import './Planner.css';
import { Tooltip, Calendar, Badge, Radio, Timeline, Form, Button, Input, Select, Modal, Icon, } from 'antd';
import moment from 'moment';

export default class extends React.Component {
    state = {
        mode: "list",
        listDays: Number.isInteger(window.foodnet.listDays) ? window.foodnet.listDays : 7,
    }

    render() {
        return (
            <>
                <div className="planner">
                    {this.renderToolbar()}
                    {this.state.mode === "calendar" && this.renderCalendar()}
                    {this.state.mode === "list" && this.renderList()}
                </div>
                {this.renderAddListItemModal()}
            </>
        );
    }

    renderToolbar() {
        return (
            <div className="planner-toolbar">
                <Radio.Group onChange={(e) => this.setState({ mode: e.target.value })} value={this.state.mode}>
                    <Radio value={"list"}>List View</Radio>
                    <Radio value={"calendar"}>Calendar View</Radio>
                </Radio.Group>
                {this.state.mode === "list" && (
                    <Input type="number" value={this.state.listDays} onChange={e => {
                        if (e.target.value.length === 0) {
                            this.setState({ listDays: undefined })
                        }
                        else {
                            let v = Number(e.target.value)
                            if (v < 0) v = 0;
                            this.setState({ listDays: v })
                            window.foodnet.listDays = v
                            window.foodnet.sync()
                        }
                    }} />
                )}
            </div>
        );
    }

    renderAddListItemModal() {
        const valid = () => {
            return this.state.recipe;
        }
        const submit = () => {
            if (this.state.eventid) {
                window.foodnet.events = window.foodnet.events.filter(e => e.id !== this.state.eventid).concat({
                    id: this.state.eventid,
                    date: moment().add(this.state.dayOffset, "days").format(),
                    qty: Number(this.state.qty || 1),
                    recipe: this.state.recipe,
                })
            }
            else {
                window.foodnet.events.push({
                    id: window.foodnet.events.length === 0 ? 1 : Math.max(...window.foodnet.events.map(e => e.id)) + 1,
                    date: moment().add(this.state.dayOffset, "days").format(),
                    qty: Number(this.state.qty || 1),
                    recipe: this.state.recipe,
                })
            }
            window.foodnet.sync();
            this.setState({
                eventid: undefined,
                recipe: undefined,
                qty: undefined,
                dayOffset: undefined,
                modal: undefined,
            })
        }
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 12 },
            },
        };
        return (this.state.modal === "add-list-item" || this.state.modal === "edit-list-item") && (
            <Modal
                className="list-item-modal"
                visible={true}
                onCancel={() => this.setState({ modal: undefined })}
                footer={[
                    this.state.modal === "edit-list-item" && (
                        <Button key="delete" type="danger" onClick={() => {
                            window.foodnet.events = window.foodnet.events.filter(e => e.id !== this.state.eventid);
                            window.foodnet.sync();
                            this.setState({
                                eventid: undefined,
                                recipe: undefined,
                                qty: undefined,
                                dayOffset: undefined,
                                modal: undefined,
                            })
                        }}>Delete Event</Button>
                    ),
                    <Button key="back" onClick={() => this.setState({ modal: undefined })}>Cancel</Button>,
                    <Button key="submit" type="primary" disabled={!valid()} onClick={() => submit()}>OK</Button>,
                ]}
            >
                <Form {...formItemLayout}>
                    <Form.Item validateStatus={valid() ? "success" : "error"} hasFeedback={true} label="Recipe">
                        <Select value={this.state.recipe} onChange={v => this.setState({ recipe: Number(v) })}>
                            {window.foodnet.recipes.map(r => (
                                <Select.Option key={r.id} value={r.id}>{r.name}</Select.Option>
                            ))}
                        </Select>
                    </Form.Item>
                    <Form.Item label="Quantity">
                        <Input
                            autoFocus
                            type="number"
                            defaultValue={1}
                            value={this.state.qty}
                            onChange={e => this.setState({ qty: e.target.value })}
                            onPressEnter={() => {
                                if (valid()) {
                                    submit();
                                }
                            }}
                        />
                    </Form.Item>
                </Form>
            </Modal>
        )
    }

    renderList() {
        const now_s_format = "Y-M-D";
        const createItemsForDay = (dayOffset) => {
            return window.foodnet.events
                .filter(e => {
                    return moment().add(dayOffset, "days").format(now_s_format) === moment(e.date).format(now_s_format)
                })
                .sort((a, b) => a.id < b.id ? -1 : 1)
                .map(e => (
                    <Timeline.Item key={e.id} color="green" onClick={() => {
                        this.setState({
                            eventid: e.id,
                            modal: "edit-list-item",
                            dayOffset: dayOffset,
                            recipe: e.recipe,
                            qty: e.qty,
                        })
                    }} >{e.qty} x {window.foodnet.recipes.find(r => r.id === e.recipe).name}</Timeline.Item>
                ))
        }
        const addForDay = (dayOffset) => {
            return <Timeline.Item key="add" onClick={() => {
                this.setState({
                    eventid: undefined,
                    modal: "add-list-item",
                    dayOffset: dayOffset,
                    recipe: undefined,
                    qty: undefined,
                })
            }} dot={<Icon type="plus-circle" theme="twoTone" twoToneColor="red" />}>Add</Timeline.Item>;
        }
        const createList = (dayOffset) => {
            return (
                <Timeline key={dayOffset}>
                    <Timeline.Item key="top" color="blue"><b><u>{moment().add(dayOffset, 'days').format("dddd (MMM D)")}</u></b></Timeline.Item>
                    {createItemsForDay(dayOffset)}
                    {addForDay(dayOffset)}
                </Timeline>
            )
        }
        return (
            <div className="planner-list">
                {Array.from(Array(this.state.listDays)).map((z, day) => createList(day))}
            </div>
        )
    }

    renderCalendar() {
        const now_s_format = "Y-M-D";
        const dateCellRender = (m) => {
            const listData = window.foodnet.events.filter(e => {
                return moment(e.date).format(now_s_format) === m.format(now_s_format)
            }).map(e => {
                const text = `${e.qty} x ${window.foodnet.recipes.find(r => r.id === e.recipe).name}`;
                const dayOffset = moment(e.date).startOf('day').diff(moment().startOf('day'), 'days')
                return { 
                    eid: e.id, 
                    dayOffset: dayOffset, 
                    type: dayOffset < 0 ? 'error' : dayOffset === 0 ? 'success' : 'warning', 
                    content: <Tooltip placement="top" title={text}>{text}</Tooltip> 
                };
            });
            return (
                <ul className="events">
                    {listData.map(item => (
                        <li key={item.eid}>
                            <Badge onClick={(event) => {
                                const e = window.foodnet.events.find(it => it.id === item.eid);
                                const dayOffset = item.dayOffset
                                this.setState({
                                    eventid: e.id,
                                    modal: "edit-list-item",
                                    dayOffset: dayOffset,
                                    recipe: e.recipe,
                                    qty: e.qty,
                                })
                                event.stopPropagation();
                            }} status={item.type} text={item.content} />
                        </li>
                    ))}
                </ul>
            );
        }

        const monthCellRender = (m) => {
            const listData = window.foodnet.events.filter(e => {
                return moment(e.date).month() === m.month() && moment(e.date).year() === m.year()
            })
            return (
                listData.length + " recipes"
            );
          }

        return (
            <Calendar mode={this.state.calMode || "month"} onPanelChange={(value, mode) => this.setState({calMode: mode})} dateCellRender={dateCellRender} monthCellRender={monthCellRender} onSelect={(m) => {
                if(this.state.calMode === 'year') {
                    this.setState({calMode: 'month'})
                    return;
                };
                const dayOffset = m.startOf('day').diff(moment().startOf('day'), 'days')
                this.setState({
                    eventid: undefined,
                    modal: "add-list-item",
                    dayOffset: dayOffset,
                    recipe: undefined,
                    qty: undefined,
                })
            }}/>
        );
    }
}