import React from 'react';
import { HashRouter, Route } from 'react-router-dom'
import RecipeApp from './recipe/RecipeApp'
import TestRecipeForm from './manual-test/TestRecipeForm'
import loader from './loaders/LocalStorageLoader'
import AppHeader from './AppHeader'
import './App.css';
import ImportExport from './admin/ImportExport'
import Reports from './reports/Reports';
import Planner from './planner/Planner'
import Shopper from './shopper/Shopper'
import Inventory from './inventory/Inventory'
import About from './about/About'

loader();

export default function () {
  return (
    <HashRouter hashType="noslash">
      <AppHeader />
      <Route exact path="/ImportExport" component={ImportExport} />
      <Route exact path="/TestRecipeForm" component={TestRecipeForm} />
      <Route exact path="/Reports" component={Reports} />
      <Route exact path="/Planner" component={Planner} />
      <Route exact path="/Shopper" component={Shopper} />
      <Route exact path="/Inventory" component={Inventory} />
      <Route exact path="/About" component={About} />
      <Route exact path="/" component={RecipeApp} />
    </HashRouter>
  );
}
