export default function() {
    const names = ["ingredients", "measurements", "recipes", "listDays", "events", "importUrls", "plannerDates", "plannerCheckedItems", "customSort", "customSortOrder"];
    window.foodnet = {
        sync: () => {
            names.forEach(it => localStorage.setItem(it, JSON.stringify(window.foodnet[it])));
        }
    };
    names.forEach(it => window.foodnet[it] = JSON.parse(localStorage.getItem(it)));
    names.forEach(it => {
        if (window.foodnet[it] === undefined || window.foodnet[it] === null) {
            window.foodnet[it] = [];
        }
    })
}