import React from 'react';
import './Inventory.css';
import { Select } from 'antd';
import MyInput from '../shopper/CustomInput'
import moment from 'moment';

export default class extends React.Component {
    state = {

    }
    findIngredientsUsedInEvents() {
        const { foodnet } = window;
        let list = [];
        const now = moment().startOf('day');
        const plannedDate = moment().endOf('day').add(foodnet.listDays - 1, 'days');
        foodnet.events.filter(e => moment(e.date).isBetween(now, plannedDate)).forEach(event => {
            const multiplier = Number(event.qty) || 0;
            const recipe = foodnet.recipes.find(r => r.id === event.recipe);
            recipe.ingredients.forEach(ing => {
                list.push({ id: ing.id, type: ing.type, qty: Number(ing.qty) * multiplier });
            })
        })

        const mergeDuplicates = (item, index, l) => {
            if (index > 0 && l[index - 1].id === item.id && l[index - 1].type === item.type) {
                let newIndex = index - 1;
                while (l[newIndex].deleted) newIndex--;
                l[newIndex].qty += item.qty;
                item.deleted = true;
            }
            return item;
        };
        list = list.sort((a, b) => {
            if (a.id !== b.id) {
                return a.id < b.id ? -1 : 1;
            }
            if (a.type !== b.type) {
                return a.type < b.type ? -1 : 1;
            }
            return 0;
        }).map(mergeDuplicates).filter(item => !item.deleted);

        return (
            list.map(item => ({
                    ...item,
                    name: foodnet.ingredients.find(i => i.id === item.id).name,
                    typename: foodnet.measurements.find(m => m.id === item.type).name,
                }))
                .sort((a, b) => a.name.localeCompare(b.name) || a.typename.localeCompare(b.typename))
        );
    }
    renderToolbar() {
        //4 buttons?
    }
    render() {
        /* TODO:
            planner: add recipe, show warning based on inventory (and change dot colors for list and calendar)
            shopper: add to inventory button, deduct current inventory, show deduct item icon, add hide zeroed button
            inventory: add toolbar: add from recipe, deduct using recipe, add from event date range, deduct from event date range
        */
        const { foodnet } = window;
        const onDropdownChange = (v, item) => {
            item.type = v;
            foodnet.sync();
            this.forceUpdate()
        }
        const createDropdown = (item) => (
            <Select className="inventory-list-item-type" value={item.type} onChange={v => onDropdownChange(v, item)}>
                {foodnet.measurements.map(m => (
                    <Select.Option key={m.id} value={m.id}>{m.name}</Select.Option>
                ))}
            </Select>
        )
        const isEditing = (item) => this.state.edit === item.id;
        const readOnlyClickHandler = (item, e) => this.setState({ edit: item.id })
        const onInputChange = (v, item) => {
            item.qty = v;
            foodnet.sync();
            this.forceUpdate()
        }
        const eventUsedIngredients = this.findIngredientsUsedInEvents();
        const used = (ing) => {
            const uing = eventUsedIngredients.find(u => u.id === ing.id && u.type === ing.type);
            if (!uing) return '';
            return ing.qty < uing.qty ? ' used-short' : ' used-good';
        }
        const createItem = (item, index) => (
            isEditing(item) ?
                <React.Fragment key={index}>
                    <div className={"inventory-list-item-name" + used(item)} onClick={() => this.setState({ edit: undefined })}>{item.name}</div>
                    <MyInput className="inventory-list-item-qty" type="number" min="0" max="9999" defaultValue={item.qty} onPressEnter={(e) => onInputChange(e.target.value, item)} onBlur={(e) => onInputChange(e.target.value, item)} />
                    {createDropdown(item)}
                    <div className="inventory-list-item-deselect-div" onClick={() => this.setState({ edit: undefined })} />
                </React.Fragment>
                :
                <React.Fragment key={index}>
                    <div className={'inventory-list-item-name' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.name}</div>
                    <div className={'inventory-list-item-qty' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.qty || 0}</div>
                    <div className={'inventory-list-item-type' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.type ? foodnet.measurements.find(m => m.id === item.type).name : "(no measurement type)"}</div>
                    <div className={'inventory-list-item-deselect-div'} onClick={() => this.setState({ edit: undefined })} />
                </React.Fragment>
        );
        return (
            <div className="inventory">
                <div className="inventory-list">
                    {foodnet.ingredients.map(createItem)}
                </div>
            </div>
        );
    }
}