/* eslint-disable eqeqeq */
import React from 'react';
import {Input} from 'antd';

export default class extends React.Component {
    state = {}

    render() {
        const handleChange = (e) => {
            this.setState({ value: e.target.value })
        };
        const onInputChange = () => {
            this.setState({ value: undefined })
        }
        return <Input
            {...this.props}
            value={this.state.value === "" ? this.state.value : (this.props.value || this.state.value || this.props.defaultValue)}
            onChange={(e) => handleChange(e)}
            onPressEnter={(e) => { onInputChange(); if (e.target.value !== "" && e.target.value != this.props.defaultValue) this.props.onPressEnter(e) }}
            onBlur={(e) => { onInputChange(); if (e.target.value !== "" && e.target.value != this.props.defaultValue) this.props.onBlur(e) }}
        />
    }
}