import React from 'react';
import './RecipeList.css';
import { Table, Input, Button, Icon } from 'antd';

export default class extends React.Component {
    state = {

    }

    render() {
        const sortedInfo = this.state.sortedInfo || {};
        const handleSearch = (selectedKeys, confirm) => {
            confirm();
            this.setState({ searchText: selectedKeys[0] });
        };
        const handleReset = clearFilters => {
            clearFilters();
            this.setState({ searchText: '' });
        };
        const getColumnSearchProps = dataIndex => ({
            filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
                <div style={{ padding: 8 }}>
                    <Input
                        ref={node => {
                            this.searchInput = node;
                        }}
                        placeholder={`Search ${dataIndex}`}
                        value={selectedKeys[0]}
                        onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                        onPressEnter={() => handleSearch(selectedKeys, confirm)}
                        style={{ width: 188, marginBottom: 8, display: 'block' }}
                    />
                    <Button
                        type="primary"
                        onClick={() => handleSearch(selectedKeys, confirm)}
                        icon="search"
                        size="small"
                        style={{ width: 90, marginRight: 8 }}
                    >
                        Search
                    </Button>
                    <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>Reset</Button>
                </div>
            ),
            filterIcon: filtered => (<Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />),
            onFilter: (value, record) => record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
            onFilterDropdownVisibleChange: visible => {
                if (visible) {
                    setTimeout(() => this.searchInput.select());
                }
            },
        })
        const cols = [
            {
                title: 'Recipe Name',
                dataIndex: 'name',
                ...getColumnSearchProps('name')
            },
            {
                title: 'Type',
                dataIndex: 'type'
            },
            {
                title: 'Ingredients',
                dataIndex: 'icount',
                sorter: (a, b) => a.icount < b.icount ? -1 : a.icount === b.icount ? 0 : 1,
                sortOrder: sortedInfo.columnKey === 'icount' && sortedInfo.order,
            },
        ]
        return (
            <Table
                columns={cols}
                dataSource={window.foodnet.recipes
                    .sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1)
                    .map(r => ({
                        ...r,
                        type: 'Custom',
                        icount: (r.ingredients || []).length
                    }))
                }
                pagination={false}
                rowKey={'id'}
                rowClassName={(r) => "recipe-list-item" + (window.foodnet.selected === r.id ? " selected" : "")}
                onRow={(r) => ({
                    onClick: () => {
                        window.foodnet.selected = r.id;
                        this.props.select(r.id);
                        this.setState({ selectedRowKeys: [r.id] });
                    },
                })}
                onChange={(pagination, filters, sortedInfo) => {
                    this.setState({ sortedInfo, });
                }}
            />
        );
    }
}