import XLSX from 'xlsx';

export const load = (input) => {
    const wb = XLSX.read(input, { type: 'array' });
    const r = XLSX.utils.sheet_to_json(wb.Sheets.Recipes);
    window.foodnet.recipes = r.map(it => ({ ...it, ingredients: JSON.parse(it.ingredientsJson) }));
    const i = XLSX.utils.sheet_to_json(wb.Sheets.Ingredients);
    window.foodnet.ingredients = i.map(it => ({ ...it, tags: it.tagsJson ? JSON.parse(it.tagsJson) : [] }));
    window.foodnet.measurements = XLSX.utils.sheet_to_json(wb.Sheets.Measurements);
    window.foodnet.events = XLSX.utils.sheet_to_json(wb.Sheets.Events);
    window.foodnet.sync();
}

export const save = (filename) => {
    const wb = XLSX.utils.book_new();
    const recreateRecipeIngredients = it => it.ingredients.map(it => ({...it, name: window.foodnet.ingredients.find(i => i.id === it.id).name}))
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.recipes.map(it => ({ ...it, ingredientsJson: JSON.stringify(recreateRecipeIngredients(it)) }))), "Recipes");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.ingredients.map(it => ({ ...it, tagsJson: JSON.stringify(it.tags || []) }))), "Ingredients");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.measurements), "Measurements");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.events), "Events");
    XLSX.writeFile(wb, filename || 'foodnet.xlsx');
}