import moment from 'moment'

export const isIngredientUsed = (id) => {
    if (window.foodnet.recipes.find(r => r.ingredients.find(i => i.id === id))) {
        return true;
    }
    return false;
}

export const isRecipeUsed = (id) => {
    if (window.foodnet.events.find(e => e.recipe === id)) {
        return true;
    }
    return false;
}  

export const isRecipeUsedOnlyInOldEvents = (id) => {
    if (window.foodnet.events.filter(e => moment(e.date).isAfter(moment().startOf('day'))).find(e => e.recipe === id)) {
        return false;
    }
    return true;
}  

export const isMeasurementUsed = (id) => {
    if (window.foodnet.recipes.find(r => r.ingredients.find(i => i.type === id))) {
        return true;
    }
    return false;
}