import React from 'react';
import { Button, Popconfirm } from 'antd';
import './RecipeToolbar.css';
import { isRecipeUsed, isRecipeUsedOnlyInOldEvents } from '../admin/EntityUsageChecker'

export default class extends React.Component {
    render() {
        const needsConfirm = this.props.selected && isRecipeUsed(this.props.selected) && isRecipeUsedOnlyInOldEvents(this.props.selected)
        return (
            <div className="recipe-toolbar">
                <Button onClick={() => this.props.showModal('add')}>Add</Button>
                <Button disabled={!this.props.selected} onClick={() => this.props.showModal('edit')}>Edit</Button>
                <Popconfirm
                    title="This is still used in old calendar events. Are you sure?"
                    onConfirm={() => this.props.delete()}
                    okText="Yes"
                    cancelText="No"
                    disabled={!this.props.selected || !needsConfirm}
                    placement="bottom"
                >
                    <Button disabled={!this.props.selected} onClick={() => {
                        if (!needsConfirm) this.props.delete()
                    }}>Delete</Button>
                </Popconfirm>
            </div>
        );
    }
}