import React from 'react';
import RecipeList from './RecipeList'
import RecipeToolbar from './RecipeToolbar'
import { Modal, message } from 'antd'
import RecipeForm from './RecipeForm'
import './RecipeApp.css'
import { isRecipeUsed, isRecipeUsedOnlyInOldEvents } from '../admin/EntityUsageChecker'

export default class extends React.Component {
    state = {
        modal: false
    }

    render() {
        //TODO: split add button, to: add custom recipe, add prepared food, add packaged food
        //TODO: allow another recipe to be an ingredient?
        return (
            <>
                {this.renderToolbar()}
                {this.renderList()}
                {this.renderModal()}
            </>
        );
    }

    renderList() {
        return <RecipeList selected={window.foodnet.selected} select={(id) => this.forceUpdate()} />;
    }

    renderToolbar() {
        return <RecipeToolbar selected={window.foodnet.selected} showModal={(a) => this.setState({ modal: a })} delete={() => this.handleDelete()} />
    }

    handleDelete() {
        if (isRecipeUsed(window.foodnet.selected) && !isRecipeUsedOnlyInOldEvents(window.foodnet.selected)) {
            message.error("This recipe is still being used. Please remove or replace the recipe where it is being used before deleting.")
        }
        else {
            window.foodnet.recipes = window.foodnet.recipes.filter(r => r.id !== window.foodnet.selected);
            window.foodnet.events = window.foodnet.events.filter(e => e.recipe !== window.foodnet.selected);
            window.foodnet.selected = undefined;
            window.foodnet.sync();
            this.forceUpdate();
        }
    }

    renderModal() {
        return this.state.modal && (
            <Modal width="90%" visible={true} footer={null} onCancel={() => this.setState({ modal: undefined })} maskClosable={false} keyboard={true}>
                <RecipeForm edit={this.state.modal === 'edit' ? window.foodnet.selected : undefined} close={() => this.setState({ modal: undefined })} />
            </Modal>
        )
    }
}